body {
    font-family: Arial, Helvetica, sans-serif;
    box-sizing: border-box;
    h1 {
        margin-left: 627px;
    }
    input, select{
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
    }
    input[type=submit] {
    background-color: #04AA6D;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    }
    .container {
    border-radius: 5px;
   background-color: #f2f2f2;
   padding: 20px;
   }
}